#gradient {
    width: inherit;
	padding: 0px;
	margin: 0px;
	z-index: 0;
	background: gradient(linear, 0 0, 100% 100%, from($lime-green), to($teal));
	background: linear-gradient(to right top, $lime-green, $teal); /* For Safari 5.1 to 6.0 */ /* For Opera 11.1 to 12.0 */ /* For Firefox 3.6 to 15 */ /* Standard syntax (must be last) */	

}

.bg {
	background: url(img/body_bg.svg) repeat;
	background-size: 400px 400px;
	z-index: 100;
	padding: 0;
	margin: 0;
	background-attachment: fixed;
}

.services {
	background-color: rgba(0, 0, 0, 0.7);
	padding: 50px 25px 100px 25px;
	
}

.services-box {
	margin: 0;
	background: white;
	padding: 60px 15px 15px 15px;
	box-sizing: border-box;
	text-align: center;
	/*border-radius: 5px;*/
	height: 225px;
	@media ($screen-sm) {
		margin: 15px auto;
	}
	@media ($screen-xs) {
		margin: 15px auto;
	}
}

.services-box h3 {
	color: black;
	font-family: $accent-font;
	font-size: 1.8em;
	text-transform: none;
	letter-spacing: 0;
	background: $orange;
	background: rgba($orange, .9);
	/*border-radius: 0 0 5px 5px;*/
	margin: 0 auto;
	padding: 15px;
	box-sizing: border-box;
	position: absolute;
	top: -10px;
}

