.services-hover-box {
	opacity: 0;
	height: 225px;
	background: rgba(250,250,250,.92);
	position: absolute;
	top: 0;
	left: 0;
	z-index: 10;
	/*border-radius: 5px;*/
	text-align: center;
	color: $pink;
	margin: 0 15px;
	padding: 50px 15px 15px 15px;
	box-sizing: border-box;
	text-transform: uppercase;
	letter-spacing: .1em;
	line-height: 1.6em;
	font-size: 1.2em;
	transition: $transition;
	cursor: pointer;
	@media ($screen-sm) {
		margin: 0 15px;
	}
	@media ($screen-xs) {
		margin: 0 15px;
	}
}

.services-hover-box:hover {
	opacity: 1;
	transition: $transition;
}

.hideme {
    opacity: 0;
}

.fadein500, .fadein600, .fadein700 {
	opacity: 0;
}